import "primereact/resources/themes/lara-light-indigo/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import "primeicons/primeicons.css";

import { AutoComplete } from "primereact/autocomplete";
import { Button } from "primereact/button";
import { OrderList } from "primereact/orderlist";

import { useState, useEffect } from "react";

const countries = ["40.1 Formative 1", "41.1 Formative 2"];

export default function App() {
  const [selectedCountry, setSelectedCountry] = useState(null);
  const [filteredCountries, setFilteredCountries] = useState(countries);
  const [formatives, setFormatives] = useState([]);

  const searchCountry = (event) => {
    let filteredCountries = countries.filter(
      (country) =>
        country.substring(0, event.query.length) === event.query &&
        formatives.includes(country) === false
    );
    setFilteredCountries(filteredCountries);
  };

  const handleAdd = (event) => {
    console.log("Added", selectedCountry);
    setFormatives([selectedCountry, ...formatives]);
    setSelectedCountry(null);
  };

  const itemTemplate = (item) => {
    return <div className="formative">{item}</div>;
  };

  return (
    <div className="App">
      {selectedCountry && <div>{selectedCountry}</div>}
      <div>
        <AutoComplete
          value={selectedCountry}
          suggestions={filteredCountries}
          completeMethod={searchCountry}
          onChange={(e) => setSelectedCountry(e.value)}
        />
        <Button label="Add" onClick={handleAdd} />
      </div>
      <div>
        <OrderList
          value={formatives}
          itemTemplate={itemTemplate}
          header="Products"
          onChange={(e) => setFormatives(e.value)}
        ></OrderList>
      </div>
      <div>
        <i className="pi pi-check mr-2"></i>
      </div>
    </div>
  );
}
